.status {
 position: relative;
  background-color: #333333;
	font-family: 'Ubuntu Mono';
  border: 2px solid white;
	padding: 4px;
  font-size: 7px;
	font-size: 0.7rem;
  color: #FFFFFF;
  width: 80px;
	width: 8rem;
	text-align: left;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;


}


.square {
  background: #fff;
  color: #87CEEB;
  border: 1px solid #999;
  float: left;
  font-size: 20px;
	font-size: 2rem;
  font-weight: bold;
  line-height: 34px;
  height: 38px;
	height: 3.8rem;
  margin-right: -1px;
  margin-top: -1px;
  padding: 4px;
  text-align: center;
  width: 38px;
	width: 3.8rem;
}


.redsquare {
  background: #ffff80;
  border: 1px solid #999;
  float: left;
  font-size: 20px;
	font-size: 2rem;
  font-weight: bold;
  line-height: 34px;
  height: 38px;
	height: 3.8rem;
  margin-right: -1px;
  margin-top: -1px;
  padding: 4px;
  text-align: center;
  width: 38px;
	width: 3.8rem;
}


.square:focus {
  outline: none;
}


.button {
  position: relative;
  background-color: #87CEEB;
  font-size: 7px;
	font-size: 0.7rem;
  color: #FFFFFF;
  width: 60px;
	width: 6rem;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
}

.button:after {
  content: "";
  background: #90EE90;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px!important;
  margin-top: -120%;
  opacity: 0;
  -webkit-transition: all 0.8s;
  -o-transition: all 0.8s;
  transition: all 0.8s
}

.button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s
}

.button:hover {
  background-color: #3BB9FF;
  color: #FEFDE3;
}


